import React, { useState } from "react";
import CustomImage from "../components/ui/CustomImage";
import HeaderNav from "../components/common/HeaderNav";

export default function page() {
  return (
    <div className="wrapper">
      <HeaderNav></HeaderNav>
      <div className="container">
        <div className="mt-[70px] flex px-[10%] text-white">
          <div className="text-[22px] font-nekstmedium">
            <div>
              <CustomImage
                src="/pages/UserPage/user_logo.jpg"
                width={100}
                height={100}
                alt="user_logo"
                className=""
              ></CustomImage>
            </div>
            <p className="text-[20px] mb-[7px]">Lolla Smith</p>
            <p className="text-[16px] font-nekstregular">lollasmith@demo.com</p>
            <ul className="mt-[20px] space-y-[15px]">
              <li>
                <button className="active:bg-purple-600 focus:bg-purple-600  rounded-[5px]">
                  <p className="inline-block px-[20px] py-[8px]">Profile</p>
                </button>
              </li>
            </ul>
          </div>
          <div></div>
        </div>
      </div>
    </div>
  );
}
